.iframewidget-container[data-v-3f534331] {
    height: 100%;
    min-height: 100px;
    overflow: hidden;
}
.iframewidget-frame[data-v-3f534331] {
    height: 100%;
    border: none;
    overflow: hidden;
}
.widget-loading[data-v-3f534331],
.widget-error[data-v-3f534331],
.widget-empty[data-v-3f534331] {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 200px;
    width: 100%;
    color: var(--color-text-maxcontrast);
	text-align: center;
    flex-direction: column;
}

/* CSP error display styling */
.widget-error.csp-error[data-v-3f534331] {
    display: flex;
    flex-direction: column;
    padding: 16px;
    text-align: center;
}

/* Error title styling */
.widget-error .error-title[data-v-3f534331] {
    font-weight: bold;
    margin-bottom: 8px;
    color: var(--color-text-maxcontrast);
}

/* Error message paragraph styling */
.widget-error p[data-v-3f534331] {
    margin: 8px 0;
    color: var(--color-text-maxcontrast);
}

/* Container for action buttons in error state */
.error-actions[data-v-3f534331] {
    margin-top: 16px;
}

/* Button styling for error actions */
.error-actions .button[data-v-3f534331] {
    display: inline-block;
    padding: 8px 16px;
    text-decoration: none;
    border-radius: var(--border-radius);
    background-color: var(--color-primary);
    color: var(--color-primary-text);
}

/* Hover state for action buttons */
.error-actions .button[data-v-3f534331]:hover {
    background-color: var(--color-primary-element-hover);
}

